// app/dashboard/transactions/page.tsx
'use client';

import { useEffect, useMemo, useState } from 'react';
import { apiFetch, isAuthenticated } from '@/src/lib/api/api-client';
import TransactionsTable from '@/src/components/dashboard/TransactionsTable';
import PageHeader from '@/src/components/shared/PageHeader';
import { Input } from '@/src/components/ui/input';
import { Button } from '@/src/components/ui/button';

export default function TransactionsPage() {
  const [txs, setTxs] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [query, setQuery] = useState('');
  const [status, setStatus] = useState<string>('all');
  const totals = useMemo(() => {
    const sent = txs.filter(t => String(t.transaction_type || t.type || '').toLowerCase().includes('payment')).reduce((s, t) => s + Number(t.amount || t.net_amount || 0), 0);
    const received = txs.filter(t => String(t.transaction_type || t.type || '').toLowerCase().includes('receive')).reduce((s, t) => s + Number(t.amount || t.net_amount || 0), 0);
    const topup = txs.filter(t => String(t.category || '').toLowerCase().includes('top')).reduce((s, t) => s + Number(t.amount || t.net_amount || 0), 0);
    return { sent, received, topup };
  }, [txs]);

  useEffect(() => {
    if (!isAuthenticated()) return;
    (async () => {
      try {
        const res = await apiFetch('/payments?limit=50&offset=0');
        setTxs(Array.isArray(res?.transactions) ? res.transactions : []);
      } catch (e: any) {
        setError(e?.message || 'Failed to load transactions');
      } finally {
        setLoading(false);
      }
    })();
  }, []);

  return (
    <div className="space-y-6">
      <PageHeader title="Transaction List" subtitle="Your recent activity." />

      {loading && <div className="bg-white rounded-xl border p-6">Loading transactions...</div>}
      {error && <div className="bg-red-50 text-red-600 border border-red-200 rounded-xl p-4">{error}</div>}

      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <div className="bg-white rounded-xl border p-4">
          <div className="text-xs text-slate-500">Total Money Sent</div>
          <div className="text-2xl font-bold mt-1 text-slate-900">{totals.sent.toLocaleString(undefined, { maximumFractionDigits: 2 })}</div>
        </div>
        <div className="bg-white rounded-xl border p-4">
          <div className="text-xs text-slate-500">Total Money Received</div>
          <div className="text-2xl font-bold mt-1 text-slate-900">{totals.received.toLocaleString(undefined, { maximumFractionDigits: 2 })}</div>
        </div>
        <div className="bg-white rounded-xl border p-4">
          <div className="text-xs text-slate-500">Total Topup</div>
          <div className="text-2xl font-bold mt-1 text-slate-900">{totals.topup.toLocaleString(undefined, { maximumFractionDigits: 2 })}</div>
        </div>
      </div>

      <div className="bg-white rounded-xl border p-4 flex flex-wrap gap-4 items-center">
        <div className="w-full md:w-64">
          <Input placeholder="Search reference or amount" value={query} onChange={(e) => setQuery(e.target.value)} className="text-slate-900 placeholder:text-slate-500 bg-white" />
        </div>
        <div>
          <select className="border rounded-md px-3 py-2 text-sm text-slate-900 bg-white" value={status} onChange={(e) => setStatus(e.target.value)}>
            <option value="all">All</option>
            <option value="completed">Completed</option>
            <option value="pending">Pending</option>
            <option value="failed">Failed</option>
          </select>
        </div>
        <div className="ml-auto flex gap-2">
          <Button variant="outline" className="px-3">Filter By Month</Button>
          <Button variant="outline" className="px-3">Export</Button>
        </div>
      </div>

      <TransactionsTable transactions={txs.filter((t) => {
        const q = query.trim().toLowerCase();
        const matchesQuery = q ? (String(t.reference || '').toLowerCase().includes(q) || String(t.amount || '').toLowerCase().includes(q)) : true;
        const matchesStatus = status === 'all' ? true : String(t.status || '').toLowerCase() === status;
        return matchesQuery && matchesStatus;
      })} />
    </div>
  );
}
