import { Table, TableHeader, TableBody, TableRow, TableHead, TableCell } from '@/src/components/ui/table';

interface TransactionsTableProps {
  transactions: Array<any>;
}

export default function TransactionsTable({ transactions }: TransactionsTableProps) {
  return (
    <div className="bg-white rounded-xl border overflow-hidden">
      <Table className="min-w-full">
        <TableHeader>
          <TableRow className="bg-slate-50 text-left text-sm text-slate-600">
            <TableHead className="px-4 py-3">Date</TableHead>
            <TableHead className="px-4 py-3">Reference</TableHead>
            <TableHead className="px-4 py-3">Amount</TableHead>
            <TableHead className="px-4 py-3">Currency</TableHead>
            <TableHead className="px-4 py-3">Status</TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {transactions.map((t) => (
            <TableRow key={String(t.id || t.reference || Math.random())} className="text-sm text-slate-900">
              <TableCell className="px-4 py-3">{String(t.created_at || t.initiated_at || '')}</TableCell>
              <TableCell className="px-4 py-3 font-mono">{String(t.reference || t.id || '')}</TableCell>
              <TableCell className="px-4 py-3 font-semibold">{String(t.amount ?? t.net_amount ?? 0)}</TableCell>
              <TableCell className="px-4 py-3">{String(t.currency || '')}</TableCell>
              <TableCell className="px-4 py-3">{String(t.status || '')}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </div>
  );
}
